nts/IComponentProps';\nimport { IAsyncComponentCreator } from '../../../domain/presentation/IAsyncComponentCreator';\n\nconst viewModel = container.resolve<IViewModel>(IViewModel);\nconst viewModelManager = container.resolve<IViewModelManager>(IViewModelManager);\nconst studioModelBasicsImpl = container.resolve<StudioModelBasicsImpl>(StudioModelBasicsImpl);\nconst asyncComponentCreator = container.resolve<IAsyncComponentCreator>(IAsyncComponentCreator);\nconst dialog = container.resolve<IDialog>(IDialog);\n\ninterface IProps {\n    functions: IAppSettingsEditorProps[\"functions\"];\n    callbacks: IAppSettingsEditorProps[\"callbacks\"];\n}\n\nconst executor = container.resolve<IUseCaseExecutor>(IUseCaseExecutor);\nconst objectUseCase = container.resolve<IObjectUseCase>(IObjectUseCase);\nconst props = defineProps<IProps>();\nconst settingsModel = ref<IAppSettings>();\nlet oldSettingsModel : IAppSettings = {general: {}};\nlet newSettingsModel : IAppSettings = {general: {}};\nconst showingTab = ref(<\"General\" | \"UI Theme\">(\"General\"));\nconst app = computed(() => viewModel.apps.find((app) => app.ID == viewModel.studio.appId));\n\nconst defaultWebLottieName = computed(() => {\n  // console.log('GETDEFWEB')\n  let lottieID: string;\n  let lottiePath = settingsModel.value?.general.webLoading;\n  // console.log(viewModel.studio.items.filter(i => i.objectType == 'model' && i.modelType == 'lottie'));\n  // console.log('LPW: ' , lottiePath);\n  if(lottiePath && lottiePath.length > 0){\n    lottieID = studioModelBasicsImpl.getModelIdFromPathStructure(lottiePath, 'lottie');\n    // console.log('LID: ', lottieID);\n    let retVal = viewModel.studio.items.find(i => i.ID == lottieID)?.name;\n    // console.log('RETVALWEB:', retVal);\n    // console.log('LMODElS', lottiesModels);\n    return retVal;\n  }\n  return \"\";\n})\n\nconst defaultMobileLottieName = computed(() => {\n  // console.log('GETDEFWEB')\n  let lottieID: string;\n  let lottiePath = settingsModel.value?.general.mobileLoading;\n  // console.log(viewModel.studio.items.filter(i => i.objectType == 'model' && i.modelType == 'lottie'));\n  // console.log('LPW: ' , lottiePath);\n  if(lottiePath && lottiePath.length > 0){\n    lottieID = studioModelBasicsImpl.getModelIdFromPathStructure(lottiePath, 'lottie');\n    // console.log('LID: ', lottieID);\n    let retVal = viewModel.studio.items.find(i => i.ID == lottieID)?.name;\n    // console.log('RETVALWEB:', retVal);\n    // console.log('LMODElS', lottiesModels);\n    return retVal;\n  }\n  return \"\";\n})\n\nconst modelMap = computed(() => viewModel.studio.items.map(model => {\n  return { ID: model.ID, name: model.name, type: model.objectType };\n}))\n\nfunction inputChanged(){\n    console.log('setModel Setting: ', settingsModel.value);\n    console.log('oldSettingsModel: ', oldSettingsModel);\n    console.log('newSettingsModel : ', newSettingsModel);\n    if(newSettingsModel.general.mobileLoading == oldSettingsModel.general.mobileLoading && newSettingsModel.general.siteTitle == oldSettingsModel.general.siteTitle &&\n       newSettingsModel.general.startScreen == oldSettingsModel.general.startScreen && newSettingsModel.general.webLoading == oldSettingsModel.general.webLoading)\n        props.callbacks.modified({ model: false });\n    else\n        props.callbacks.modified({ model: true });\n}\n\nprops.functions.getModel = async () => {\n    const model = JSON.stringify(settingsModel.value!);\n    return { model, state: { } };\n};\nprops.functions.setModel = async (model, state) => {\n    //model\n    // console.log('setModel Setting: ', model);\n    settingsModel.value = JSON.parse(model);\n    oldSettingsModel = JSON.parse(model);\n    newSettingsModel = JSON.parse(model);\n}\n// const modified: Record<string, EventListenerOrEventListenerObject> = {\n//     select: () => { console.log(\"select\"); props.callbacks.modified({ state: true }) },\n//     scroll: () => { console.log(\"scroll\"); props.callbacks.modified({ state: true }) },\n//     change: () => { console.log(\"change\"); props.callbacks.modified({ model: true }) },\n//     keydown: (e: Event) => {\n//         const event = e as KeyboardEvent;\n//         const code = event.code.toLowerCase();\n//         if (event.shiftKey || event.ctrlKey || code.startsWith(\"arrow\")){return;}\n//         console.log(\"keydown\"); props.callbacks.modified({ model: true });\n//     },\n//     resize: () => { console.log(\"resize\"); props.callbacks.modified({ state: true }) },\n//     input:() =>{console.log('model change'); props.callbacks.modified({model:true})}\n// };\n\n\n// const watchTextArea = (attach: boolean) => {\n//     const target = attach ? settingsModel!.value!.addEventListener : textarea.value.removeEventListener;\n//     Object.keys(modified).forEach(key => target.call(textarea.value, key, modified[key]));\n// }\n\n// function setRootPageCB() {\n//     let modelId = viewModel.studio.items.find(i => i.objectType == 'model' && i.modelType == 'qjson' \n//                                             &&  i.parent.objectType == 'application' && i.name == settingsModel.value?.general.startScreen)?.ID;\n//     executor.execute(\n//         async () => {\n//             if(modelId)\n//                 await objectUseCase.setRootScreen(modelId);\n//         },\n//         { loading: true }\n//     );\n// }\n\nconst lottiesModels = computed(() => {\n    const models: Array<String> = [];\n    viewModelManager.traverseChildren(viewModel.studio.items, (item) => {\n        if(item.objectType == \"model\" && item.modelType == \"lottie\"){ models.push(item.name); }\n        return false;\n    });\n    return models;\n});\n\n// const uiPageModels = computed(() => {\n//     const models: Array<String> = [];\n//     viewModelManager.traverseChildren(viewModel.studio.items, (item) => {\n//         viewModel.studio.items.filter(i => i.objectType == 'model' && i.modelType == 'qjson' &&  i.parent.objectType == 'application' )\n//         if(item.objectType == \"model\" && item.modelType == \"qjson\" && item.parent.objectType == 'application'){ models.push(item.name); }\n//         return false;\n//     });\n//     return models;\n// });\n\nfunction updateApp() {\n    let appParam = viewModel.apps.find(i => i.ID == viewModel.studio.appId) as IApplication;\n    dialog.showDialog(\n        defineAsyncComponent(() => import(`../components/application/addOrUpdateApp.vue`)),\n        { closable: true, title: \"Update Application\" },\n        { app: appParam}\n    );\n}\n\nfunction changeAppLogo() {\n    let appParam = viewModel.apps.find(i => i.ID == viewModel.studio.appId);\n    dialog.showDialog(\n        defineAsyncComponent(() => import(`../components/dialogs/appLogoDialog.vue`)),\n        { closable: true, title: \"Select App Image\", width: \"530px\" },\n        { app: appParam }\n    );\n}\n\n// async function onStartScreenSelect(screenName: string){\n//     settingsModel.value!.general.startScreen = screenName;\n//     inputChanged();\n// }\n\nfunction onWebLottieSelect(lottieName: string){\n    // settingsModel.value!.general.webLoading = lottieName;\n    const modelID = getModelIDInModelMapByName(lottieName);\n    const val = studioModelBasicsImpl.createPathStructureByModelId(modelID!, 'lottie' as ModelType);\n    settingsModel.value!.general.webLoading = val;\n    newSettingsModel.general.webLoading = val;\n    inputChanged();\n}\n\nfunction onMobileLottieSelect(lottieName: string){\n    const modelID = getModelIDInModelMapByName(lottieName);\n    const val = studioModelBasicsImpl.createPathStructureByModelId(modelID!, 'lottie' as ModelType);\n    settingsModel.value!.general.mobileLoading = val;\n    newSettingsModel.general.mobileLoading = val;\n    inputChanged();\n}\n\nfunction onInputSiteTitle($event: any) {\n    settingsModel.value!.general.siteTitle = $event.target.value;\n    newSettingsModel.general.siteTitle = $event.target.value;\n    inputChanged();\n}\n\nfunction importDialog() {\n    dialo