p>\n          <input class= \"textbox\" style= \"background: #E3E3E3;\" readonly=\"true\" disabled=\"true\" :value=\"app?.name\"/>\n          <div class=\"appnameediticon\"><i class=\"mdi mdi-pencil\" @click=\"updateApp\"></i></div>\n        </div>\n        <div class= \"contentinnerdiv\" style=\"top:86px;\">\n          <p class=\"combotext\"><b>Site title</b></p>\n          <i class=\"mdi mdi-information informationicon\"></i>\n          <span class=\"info-tooltip\">\n            This title will be displayed in the web browser.\n          </span>\n          <input class= \"textbox\" type=\"text\" @input=\"onInputSiteTitle\" :value=\"settingsModel?.general.siteTitle\"/>\n        </div>\n        <!-- <div class= \"contentinnerdiv\" style=\"top:176px;\">\n          <p class=\"combotext\"><b>Start screen</b></p>\n          <CustomSelect @input=\"onStartScreenSelect\" \n                :options = uiPageModels\n                :default = \"(uiPageModels.find(i => i == settingsModel!.general!.startScreen) as string)\"\n                :selectedClass = \"'selected-lottie'\"\n                class=\"select-lottie\">\n          </CustomSelect>\n        </div> -->\n        \n        <div class= \"contentinnerdiv\" style=\"top:176px;\">\n          <!-- <div class= \"contentinnerdiv\" style=\"top:262px;\"> -->\n          <p class=\"combotext\"><b>Web loading animation</b></p>\n          <CustomSelect @input=\"onWebLottieSelect\" @click=\"addEventListeners()\" \n                :options = \"lottiesModels\" :placeholder=\"'Select'\"\n                :default = \"defaultWebLottieName\" :selected-option=\"defaultWebLottieName\" :iconInSelect = \"true\"\n                :selectedClass = \"'selected-lottie'\" :extra-button=\"true\" :buttons=\"[{buttonText: '+ Add New', buttonClass: 'add-new-lottie-in-options', buttonID: 'add-web-lottie'} ]\" \n                class=\"select-lottie\">\n          </CustomSelect>\n        </div>\n        <div class= \"contentinnerdiv\" style=\"top:262px