function updateApp() {
    let appParam = viewModel.apps.find(i => i.ID == viewModel.studio.appId) as IApplication;
    dialog.showDialog(
        defineAsyncComponent(() => import(`../components/application/addOrUpdateApp.vue`)),
        { closable: true, title: "Update Application" },
        { app: appParam}
    );
}

function changeAppLogo() {
    let appParam = viewModel.apps.find(i => i.ID == viewModel.studio.appId);
    dialog.showDialog(
        defineAsyncComponent(() => import(`../components/dialogs/appLogoDialog.vue`)),
        { closable: true, title: "Select App Image", width: "530px" },
        { app: appParam }
    );
}

